'use client';

import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { siteData } from '@/data/siteData';
import { Card } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { ArrowLeft, ArrowUpRight, Compass, Map, Palmtree, Car } from 'lucide-react';

const categories = {
    tours: { label: 'Cultural Tours', image: '/images/hero-marrakech.jpg', desc: 'Immerse yourself in history.', icon: Map },
    activities: { label: 'Adventures', image: '/images/hero-marrakech.jpg', desc: 'Thrills in the desert.', icon: Compass },
    packages: { label: 'Curated Packs', image: '/images/hero-marrakech.jpg', desc: 'Complete experiences.', icon: Palmtree },
    transport: { label: 'Transport', image: '/images/hero-marrakech.jpg', desc: 'Travel in comfort.', icon: Car },
};

// Moroccan Pattern SVG Background
const PatternBackground = () => (
    <div className="absolute inset-0 opacity-[0.03] pointer-events-none"
        style={{ backgroundImage: 'radial-gradient(circle at 2px 2px, #C04A2F 1px, transparent 0)', backgroundSize: '40px 40px' }}>
    </div>
);

export const ServicesPageContent = ({ locale }: { locale: string }) => {
    const [selectedCategory, setSelectedCategory] = useState<string | null>(null);

    const getItems = () => {
        if (selectedCategory === 'tours') return siteData.tours;
        if (selectedCategory === 'activities') return siteData.activities.experiences;
        if (selectedCategory === 'packages') return siteData.packages;
        if (selectedCategory === 'transport') return siteData.transport;
        return [];
    };

    const renderPrice = (item: any) => {
        if (item.price) return <span className="text-primary font-bold text-lg">{item.price}€</span>;
        if (item.pricing && item.pricing[0]) {
            const p = item.pricing[0];
            return <span className="text-primary font-bold text-lg">{p.totalPrice ? `${p.totalPrice}€` : `${p.pricePerPerson}€/p`}</span>;
        }
        return null;
    };

    return (
        <div className="min-h-screen bg-[#FAFAF9] text-neutral-dark relative overflow-hidden">
            <PatternBackground />

            <div className="container mx-auto px-4 pt-32 pb-20 relative z-10">

                <AnimatePresence mode="wait">
                    {!selectedCategory ? (
                        <motion.div
                            key="categories"
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            exit={{ opacity: 0, scale: 0.95 }}
                            transition={{ duration: 0.5 }}
                            className="h-full"
                        >
                            <div className="text-center mb-16">
                                <span className="font-poppins font-semibold text-primary text-xl italic mb-3 block">Discover Morocco</span>
                                <h1 className="text-5xl md:text-7xl font-bold font-poppins text-neutral-dark mb-6 tracking-tight">
                                    Curated <span className="text-primary/80">Experiences</span>
                                </h1>
                            </div>

                            {/* Mosaic / Bento Grid Layout */}
                            <div className="grid grid-cols-1 lg:grid-cols-12 gap-6 h-auto lg:h-[70vh] min-h-[600px]">

                                {/* TOURS: Large Main Block (Left) */}
                                <motion.div
                                    className="lg:col-span-7 relative rounded-[2.5rem] overflow-hidden cursor-pointer group shadow-lg"
                                    onClick={() => setSelectedCategory('tours')}
                                    whileHover={{ scale: 0.99 }}
                                    transition={{ duration: 0.4 }}
                                >
                                    <div className="absolute inset-0 bg-cover bg-center transition-transform duration-1000 group-hover:scale-110" style={{ backgroundImage: `url(${categories.tours.image})` }}></div>
                                    <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/20 to-transparent opacity-80 group-hover:opacity-90 transition-opacity"></div>
                                    <div className="absolute bottom-0 left-0 p-10 text-white">
                                        <div className="bg-white/10 backdrop-blur-md p-4 rounded-2xl w-fit mb-4">
                                            <categories.tours.icon className="w-8 h-8" />
                                        </div>
                                        <h2 className="text-5xl font-bold font-poppins mb-2">{categories.tours.label}</h2>
                                        <p className="text-xl font-light opacity-80">{categories.tours.desc}</p>
                                    </div>
                                    <div className="absolute top-8 right-8 bg-white text-primary rounded-full p-4 opacity-0 group-hover:opacity-100 transform translate-x-4 group-hover:translate-x-0 transition-all duration-500">
                                        <ArrowUpRight className="w-6 h-6" />
                                    </div>
                                </motion.div>

                                {/* Right Column */}
                                <div className="lg:col-span-5 flex flex-col gap-6 h-full">

                                    {/* ACTIVITIES: Top Right */}
                                    <motion.div
                                        className="flex-1 relative rounded-[2.5rem] overflow-hidden cursor-pointer group shadow-lg"
                                        onClick={() => setSelectedCategory('activities')}
                                        whileHover={{ scale: 0.99 }}
                                        transition={{ duration: 0.4 }}
                                    >
                                        <div className="absolute inset-0 bg-cover bg-center transition-transform duration-1000 group-hover:scale-110" style={{ backgroundImage: `url(${categories.activities.image})` }}></div>
                                        <div className="absolute inset-0 bg-gradient-to-t from-black/70 via-transparent to-transparent opacity-80 group-hover:opacity-90 transition-opacity"></div>
                                        <div className="absolute bottom-0 left-0 p-8 text-white">
                                            <div className="flex items-center gap-3 mb-2">
                                                <categories.activities.icon className="w-6 h-6 text-primary" />
                                                <h2 className="text-3xl font-bold font-poppins">{categories.activities.label}</h2>
                                            </div>
                                            <p className="text-sm font-light opacity-80">{categories.activities.desc}</p>
                                        </div>
                                    </motion.div>

                                    {/* Bottom Split: Packages & Transport */}
                                    <div className="flex-1 flex gap-6">

                                        {/* PACKAGES */}
                                        <motion.div
                                            className="flex-1 relative rounded-[2.5rem] overflow-hidden cursor-pointer group shadow-lg"
                                            onClick={() => setSelectedCategory('packages')}
                                            whileHover={{ scale: 0.99 }}
                                            transition={{ duration: 0.4 }}
                                        >
                                            <div className="absolute inset-0 bg-cover bg-center transition-transform duration-1000 group-hover:scale-110" style={{ backgroundImage: `url(${categories.packages.image})` }}></div>
                                            <div className="absolute inset-0 bg-black/40 group-hover:bg-black/50 transition-colors"></div>
                                            <div className="absolute inset-0 flex flex-col items-center justify-center text-white text-center p-4">
                                                <categories.packages.icon className="w-10 h-10 mb-3 text-white/90" />
                                                <h2 className="text-2xl font-bold font-poppins">{categories.packages.label}</h2>
                                            </div>
                                        </motion.div>

                                        {/* TRANSPORT */}
                                        <motion.div
                                            className="flex-1 relative rounded-[2.5rem] overflow-hidden cursor-pointer group shadow-lg"
                                            onClick={() => setSelectedCategory('transport')}
                                            whileHover={{ scale: 0.99 }}
                                            transition={{ duration: 0.4 }}
                                        >
                                            <div className="absolute inset-0 bg-neutral-800 transition-colors group-hover:bg-primary duration-500"></div>
                                            <div className="absolute inset-0 flex flex-col items-center justify-center text-white text-center p-4">
                                                <categories.transport.icon className="w-10 h-10 mb-3 text-white/90" />
                                                <h2 className="text-2xl font-bold font-poppins">{categories.transport.label}</h2>
                                                <div className="mt-2 w-8 h-[2px] bg-white/30 group-hover:w-16 transition-all duration-300"></div>
                                            </div>
                                        </motion.div>
                                    </div>
                                </div>
                            </div>
                        </motion.div>
                    ) : (
                        <motion.div
                            key="list"
                            initial={{ opacity: 0, y: 50 }}
                            animate={{ opacity: 1, y: 0 }}
                            exit={{ opacity: 0, y: 50 }}
                            transition={{ duration: 0.5 }}
                        >
                            <div className="flex items-center justify-between mb-12">
                                <button
                                    onClick={() => setSelectedCategory(null)}
                                    className="px-6 py-2 rounded-full border border-neutral-200 hover:border-primary hover:text-primary transition-all flex items-center gap-2 font-medium bg-white shadow-sm"
                                >
                                    <ArrowLeft className="w-4 h-4" /> Back to Collections
                                </button>
                                <h2 className="text-3xl font-bold font-poppins capitalize hidden md:block">{categories[selectedCategory as keyof typeof categories].label}</h2>
                            </div>

                            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                                {getItems().map((item: any) => (
                                    <motion.div
                                        key={item.id}
                                        initial={{ opacity: 0, scale: 0.9 }}
                                        whileInView={{ opacity: 1, scale: 1 }}
                                        viewport={{ once: true }}
                                        className="group"
                                    >
                                        <Card className="h-full border-none shadow-md hover:shadow-xl transition-shadow duration-300 rounded-[2rem] overflow-hidden flex flex-col bg-white">
                                            <div className="relative h-72 overflow-hidden">
                                                <div
                                                    className="absolute inset-0 bg-cover bg-center transition-transform duration-700 group-hover:scale-110"
                                                    style={{ backgroundImage: 'url(/images/hero-marrakech.jpg)' }}
                                                ></div>
                                                <div className="absolute top-4 right-4 bg-white/95 backdrop-blur px-4 py-2 rounded-full shadow-sm text-sm font-bold text-neutral-dark">
                                                    {renderPrice(item)}
                                                </div>
                                            </div>
                                            <div className="p-8 flex flex-col flex-grow relative">
                                                {/* Decorative line */}
                                                <div className="w-12 h-1 bg-primary mb-6"></div>

                                                <h3 className="text-2xl font-bold font-poppins text-neutral-dark mb-4 group-hover:text-primary transition-colors">
                                                    {item.name || item.type}
                                                </h3>
                                                <p className="text-gray-500 font-light leading-relaxed mb-8 flex-grow">
                                                    {item.desc || "Experience the authentic beauty of Morocco with our expertly guided service."}
                                                </p>

                                                <Button
                                                    href={`/${locale}/${selectedCategory === 'activities' ? 'activities' : selectedCategory}/${item.id}`}
                                                    className="w-full justify-between group-hover:bg-primary group-hover:text-white transition-all bg-neutral-100 text-neutral-dark"
                                                >
                                                    <span>View Details</span>
                                                    <ArrowUpRight className="w-4 h-4" />
                                                </Button>
                                            </div>
                                        </Card>
                                    </motion.div>
                                ))}
                            </div>
                        </motion.div>
                    )}
                </AnimatePresence>
            </div>
        </div>
    );
};
